import React, { PropTypes } from 'react';
import { observer } from 'mobx-react'

import './Badges.sass'

@observer
export default class Badges extends React.Component {
  static propTypes = {
    project: PropTypes.string.isRequired
  };
  render() {
    let project = this.props.project
    let techBadges = this.props.store[project].Tech

    return (
        <div className='project-badges'>
        <div className=' snap'>
          <h6>TYPE</h6>
          <span className='badge'>{this.props.store[project].Type}</span>
        </div>
        <div className='snap'>
          <h6>ROLE</h6>
          <span className='badge'>{this.props.store[project].Role}</span>
        </div>
        <div className='snap'>
          <h6>TECH</h6>
          {techBadges.map((tech, i) => <span className='badge' key={tech}>{tech}</span>)}
        </div>
      </div>

    );
  }
}



